import _ from 'lodash'

/**
 * 生成树结构
 * @param  {[type]} menus [description]
 * @return {[type]}       [description]
 */
function generatorMenus(menus){
	//加入相关组件
	_.each(menus, (m) =>{
		addComponent(m);
	})
	//获取根节点
	const menus_ = _.filter(menus, (m) => m.parent.length == 0);
	//排序
	const sortMenus = _.clone(_.sortBy(menus_, ['order']));
	//获取子节点
	const children = _.difference(menus, menus_);
	//子节点加入父节点中
	_.each(children, (child) =>{
		const index = _.findIndex(sortMenus, (m) => {
			return m._id.toString() === child.parent.toString();
		})
		if(!sortMenus[index].children){
			sortMenus[index].children = [];
		}
		sortMenus[index].children.push(child);
	})
	//先做子排序
	_.each(sortMenus, (m)=>{
		const sortChild = _.sortBy(m.children, ['order']);
		m.children = sortChild;
	})
	return sortMenus;
}

/**
 * 加入组件
 * @param {[type]} menu [description]
 */
function addComponent(menu){
	const path = menu.componentUrl;
	menu.component = require('../' + path);
}

function getMenus(state){
	//判断是否有本地数据
	if(state.menus.length > 0){
		return state.menus;
	}
	let session_menus = sessionStorage.getItem('menus');
	if(session_menus){
		session_menus = JSON.parse(session_menus);
	}else{
		session_menus = [];
	}
	return session_menus;
}

function getAccount(state){
	//判断当前是否有账户数据
	if(state.account.mobile){
		return state.account;
	}
	const session = sessionStorage.getItem('session');
	let account = {};
	if(session){
		account = JSON.parse(session).account;
	}
	return account;
}

// export default getters
const menuitems = state => generatorMenus(getMenus(state));
const menus = state => getMenus(state);
const account = state => getAccount(state);
const session = state => JSON.parse(sessionStorage.getItem('session'));

export {
	session,
	menuitems,
	menus,
	account
	// isLoadRoutes
}


